import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
	name: String;
	username: String;
	email: String;
	password: String;


  constructor(private validateService: ValidateService, 
  	private flashMessage: FlashMessagesService,
  	private authService: AuthService,
  	private router: Router) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
  	const user = {
  		name: this.name,
  		email: this.email,
  		username: this.username,
  		password: this.password
  	}

  	//require fields
  	if(!this.validateService.validateRegister(user)){
  		this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeout:3000});
  		return false;
  	}

	if(!this.validateService.validateEmail(user.email)){
  		this.flashMessage.show('Please insert a valida email', {cssClass: 'alert-danger', timeout:3000});
  		return false;
  	}

  	// Register User
  	 this.authService.registerUser(user).subscribe(data => { 
  	 	this.flashMessage.show('Registration Complete', { classes: ['alert', 'alert-success'], timeout: 3000 });
  	 	 this.router.navigate(['/login']);
  	 	}, error=>{ 
  	 	if(error){
  	 	 this.flashMessage.show('Oops! Somethin went wrong',{ classes: ['alert', 'alert-danger'], timeout: 3000 });
  	 	 this.router.navigate(['/register']);

  	 	}
  	 });




  }


}



